import '../App.css';
import '../css/Common.css';
import '../css/Main.css';
import SubHeader from './SubHeader';
import React, { useState, useCallback, useEffect, useRef, any } from 'react';

import { Link } from 'react-router-dom';
import Masonry, {ResponsiveMasonry} from "react-responsive-masonry"
import gallery_1 from '../img/gallery-1.jpg'
import gallery_2 from '../img/gallery-2.jpg'
import gallery_3 from '../img/gallery-3.jpg'
import gallery_4 from '../img/gallery-4.jpg'
import gallery_5 from '../img/gallery-5.jpg'
import gallery_6 from '../img/gallery-6.jpg'
import gallery_7 from '../img/gallery-7.jpg'
import gallery_8 from '../img/gallery-8.jpg'
import gallery_9 from '../img/gallery-9.jpg'

import { render } from "react-dom";
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";

import { photos } from "./photos";
import main from "../json/main.json";

const images = [
  gallery_1,
  gallery_2,
  gallery_3,
  gallery_4,
  gallery_5,
  gallery_6,
  gallery_7,
  gallery_8,
  gallery_9,
]


//function importAll(r) {
//  return r.keys().map(r);
//}
//const photos = importAll(require.context('./', false, /\.(png|jpg|jpe?g|svg)$/));
//alert(images);
function Main() {

  const contentRefs = useRef([]);
  
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };


  return (
    <div className="App">

      {/* <div>

        <img className="App-main-img-1" src="/img/main_color_crop.PNG" />

      </div> */}

      <header className="App-header">
      <SubHeader headerTitle={''} nav={main} refs={contentRefs}/>
        {/* <p className="App-main-title">
              :)
        </p> */}
        <div className="Main-img" />
        </header>
        <body className='Main-background-color Main-text-color'>
        <div className="Common-empty-20" />
        <div className="Common-empty-20" />


        {
            main.map(function(entry, index) {
                return (
                    <div className="Common-content-block" key={index} ref={ (element) => {contentRefs.current[index] = element} } >
                        <div className="Common-sub-title">{(index+1) + ". " + entry.title}</div>

                        {
                             entry.pic != "" && entry.pic != null ? 
                                <div><img className="Common-content-img" src={entry.pic} style={{width:entry.picWidthSize}}/> </div> : null
                        }

                        <div className="Common-sub-content">{entry.content}</div>

                        {
                            entry.type == "gallery" ? 
                            <div>
                              <Gallery photos={photos} onClick={openLightbox} />
                              <ModalGateway>
                                {viewerIsOpen ? (
                                  <Modal onClose={closeLightbox}>
                                    <Carousel
                                      currentIndex={currentImage}
                                      views={photos.map(x => ({
                                        ...x,
                                        srcset: x.srcSet,
                                        sizes: x.sizes,
                                        caption: x.title
                                      }))}
                                    />
                                  </Modal>
                                ) : null}
                              </ModalGateway>
                            </div> : null
                        }

                        {
                            entry.type == "comment" ? 
                            <iframe style={{border:'none'}} src="https://comment-blog.tistory.com/1" className="Main-comment-area"/>
                             : null
                        }

                        {
                            entry.link != "" && entry.link != null ? 

                            <Link to={entry.link} className="Common-link">
                              <div>{entry.linkTitle}</div>
                            </Link>
                            : null
                        }

                        {
                            entry.externalLink != "" && entry.externalLink != null ? 
                            <a
                              className="Common-link"
                              href={entry.externalLink}
                              target="_blank"
                              rel="noopener noreferrer"
                            >
                              {entry.externalLinkTitle}
                            </a> : null
                        }
                        
                        <div className="Common-empty-20" />

                    </div>
                )
            })
        } 



      <div className="App-empty-area-50"></div>
      </body>

    </div>
  );
}
// , , 

export default Main;
