import '../css/Common.css';
import '../css/Main.css';
import { Link } from 'react-router-dom';
import React, { useState, useEffect, useRef, any } from 'react';



// let classNameForTopBtn = 'Common-fixed-top-btn';

function SubHeader( {headerTitle, nav, refs} ) {

    const [isMenuOpen, setMenu] = useState(false)
    const [showButton, setShowButton] = useState(false);
    const [classNameForTopBtn, setClassNameForTopBtn] = useState('Common-fixed-top-btn');

    const toggleMenu = () => {
      setMenu(isMenuOpen => !isMenuOpen); // on,off  boolean
    };
    const scrollToTop = () => {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    };
    const scrollToEntry = (ref) => {
      console.log('ref:' + ref);
      toggleMenu();
      ref.scrollIntoView({ behavior: 'smooth' });
    };
    useEffect(() => {
      const ShowButtonClick = () => {
        if (window.scrollY > 10) {
          setShowButton(true)
        } else {
          setShowButton(false)
        }

        let scrollTop = document.documentElement.scrollTop;
        let bodyLimit = document.body.scrollHeight - window.innerHeight - 50;

//        console.log('document.body.scrollHeight : ' + document.body.scrollHeight); // height for all
//        console.log('window.innerHeight : ' + window.innerHeight); // screen height
//        console.log('document.documentElement.clientHeight : ' + document.documentElement.clientHeight); // screen height
//        console.log('document.documentElement.scrollTop : ' + document.documentElement.scrollTop); // current top height
//
//        console.log('scrollTop : ' + scrollTop); 
//        console.log('bodyLimit : ' + bodyLimit);

        if (scrollTop > bodyLimit) {
          setClassNameForTopBtn(headerTitle=='' ? "Main-absolute-top-btn":"Common-absolute-top-btn");
        } else {
          setClassNameForTopBtn(headerTitle=='' ? "Main-fixed-top-btn":"Common-fixed-top-btn");
        }
      }
      window.addEventListener("scroll", ShowButtonClick)
      return () => {
        window.removeEventListener("scroll", ShowButtonClick)
      }
  }, [])

    return (
      <div className="Common-all Common-sub-header">

            <Link to="/">
              <div className={headerTitle=='' ? "":"Common-home-btn"}></div>
            </Link>
            <div className={headerTitle=='' ? "Main-menu-btn":"Common-menu-btn"} onClick={()=>toggleMenu()}></div>

            {
                            headerTitle != "" && headerTitle != null ? 
                            <div className="Common-top-title">{headerTitle}</div>
                            : null
            }


          {showButton &&
          <div>
            <div className={classNameForTopBtn} onClick={scrollToTop} />
            {/* <button className="Common-top-btn" onClick={scrollToTop} type="button">Top</button> */}
          </div>
          }
          {/* <div className="Common-top-btn" onClick={scrollToTop}></div> */}

          <div className={isMenuOpen ? "Common-show-menu" : "Common-hide-menu"}>

            <div className="Common-close-btn" onClick={()=>toggleMenu()}/>
            <div className="Common-nav-content">{headerTitle}</div>
            {
              nav.map(function(entry, index) {
                return (
                  <div>
                    <div className="Common-nav-content" onClick={()=>scrollToEntry(refs.current[index])}>{(index+1) + ". " + entry.title}</div>
                  </div>

                );
              })
            }

          </div>
      </div>
      
    )
};

export default SubHeader;
